<template>
	<view class="container">
		<view class="header">
			<text class="title">教师课时统计调试工具</text>
		</view>
		
		<view class="content">
			<view class="section">
				<button @click="debugTeacherHours" :loading="loading" class="debug-btn">调试教师课时统计</button>
				<view v-if="debugResult" class="result">
					<text class="result-title">调试结果:</text>
					<textarea :value="formatResult(debugResult)" class="result-text" readonly></textarea>
				</view>
			</view>
			
			<view class="section">
				<button @click="debugAllTeachers" :loading="loadingAll" class="debug-btn">调试所有教师</button>
				<view v-if="allTeachersResult" class="result">
					<text class="result-title">所有教师调试结果:</text>
					<textarea :value="formatResult(allTeachersResult)" class="result-text" readonly></textarea>
				</view>
			</view>
			
			<view class="section">
				<button @click="debugScheduleData" :loading="loadingSchedule" class="debug-btn">调试课程安排数据</button>
				<view v-if="scheduleDataResult" class="result">
					<text class="result-title">课程安排数据调试结果:</text>
					<textarea :value="formatResult(scheduleDataResult)" class="result-text" readonly></textarea>
				</view>
			</view>
			
			<view class="section">
				<button @click="comprehensiveDebug" :loading="loadingComprehensive" class="debug-btn">全面数据调试</button>
				<view v-if="comprehensiveResult" class="result">
					<text class="result-title">全面数据调试结果:</text>
					<textarea :value="formatResult(comprehensiveResult)" class="result-text" readonly></textarea>
				</view>
			</view>
			
			<view class="section">
				<button @click="checkScheduleUsername" :loading="loadingCheck" class="debug-btn">检查课程安排username字段</button>
				<view v-if="checkResult" class="result">
					<text class="result-title">检查结果:</text>
					<textarea :value="formatResult(checkResult)" class="result-text" readonly></textarea>
				</view>
			</view>
			
			<view class="section">
				<button @click="fixScheduleUsername" :loading="loadingFix" class="debug-btn" style="background: #ff6b6b;">修复课程安排username字段</button>
				<view v-if="fixResult" class="result">
					<text class="result-title">修复结果:</text>
					<textarea :value="formatResult(fixResult)" class="result-text" readonly></textarea>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				loading: false,
				loadingAll: false,
				loadingSchedule: false,
				loadingComprehensive: false,
				loadingCheck: false,
				loadingFix: false,
				debugResult: null,
				allTeachersResult: null,
				scheduleDataResult: null,
				comprehensiveResult: null,
				checkResult: null,
				fixResult: null
			}
		},
		methods: {
			debugTeacherHours() {
				this.loading = true;
				uniCloud.callFunction({
					name: 'debug-teacher-hours',
					success: (res) => {
						this.debugResult = res.result;
					},
					fail: (err) => {
						console.error('调试失败:', err);
						uni.showToast({
							title: '调试失败',
							icon: 'none'
						});
					},
					complete: () => {
						this.loading = false;
					}
				});
			},
			
			debugAllTeachers() {
				this.loadingAll = true;
				uniCloud.callFunction({
					name: 'debug-all-teachers',
					success: (res) => {
						this.allTeachersResult = res.result;
					},
					fail: (err) => {
						console.error('调试所有教师失败:', err);
						uni.showToast({
							title: '调试失败',
							icon: 'none'
						});
					},
					complete: () => {
						this.loadingAll = false;
					}
				});
			},
			
			debugScheduleData() {
				this.loadingSchedule = true;
				uniCloud.callFunction({
					name: 'debug-schedule-data',
					success: (res) => {
						this.scheduleDataResult = res.result;
					},
					fail: (err) => {
						console.error('调试课程安排数据失败:', err);
						uni.showToast({
							title: '调试失败',
							icon: 'none'
						});
					},
					complete: () => {
						this.loadingSchedule = false;
					}
				});
			},
			
			comprehensiveDebug() {
				this.loadingComprehensive = true;
				uniCloud.callFunction({
					name: 'comprehensive-debug',
					success: (res) => {
						this.comprehensiveResult = res.result;
					},
					fail: (err) => {
						console.error('全面数据调试失败:', err);
						uni.showToast({
							title: '调试失败',
							icon: 'none'
						});
					},
					complete: () => {
						this.loadingComprehensive = false;
					}
				});
			},
			
			checkScheduleUsername() {
				this.loadingCheck = true;
				uniCloud.callFunction({
					name: 'check-schedule-username',
					success: (res) => {
						this.checkResult = res.result;
					},
					fail: (err) => {
						console.error('检查失败:', err);
						uni.showToast({
							title: '检查失败',
							icon: 'none'
						});
					},
					complete: () => {
						this.loadingCheck = false;
					}
				});
			},
			
			fixScheduleUsername() {
				uni.showModal({
					title: '确认修复',
					content: '确定要修复课程安排中的username字段吗？此操作不可逆。',
					success: (modalRes) => {
						if (modalRes.confirm) {
							this.loadingFix = true;
							uniCloud.callFunction({
								name: 'fix-schedule-username',
								success: (res) => {
									this.fixResult = res.result;
									uni.showToast({
										title: '修复完成',
										icon: 'success'
									});
								},
								fail: (err) => {
									console.error('修复失败:', err);
									uni.showToast({
										title: '修复失败',
										icon: 'none'
									});
								},
								complete: () => {
									this.loadingFix = false;
								}
							});
						}
					}
				});
			},
			
			formatResult(result) {
				return JSON.stringify(result, null, 2);
			}
		}
	}
</script>

<style>
	.container {
		padding: 20rpx;
	}
	
	.header {
		text-align: center;
		margin-bottom: 40rpx;
	}
	
	.title {
		font-size: 36rpx;
		font-weight: bold;
		color: #333;
	}
	
	.content {
		background: #fff;
		border-radius: 10rpx;
		padding: 30rpx;
		box-shadow: 0 2rpx 10rpx rgba(0,0,0,0.1);
	}
	
	.section {
		margin-bottom: 40rpx;
	}
	
	.debug-btn {
		width: 100%;
		height: 80rpx;
		line-height: 80rpx;
		background: #007AFF;
		color: #fff;
		border-radius: 10rpx;
		font-size: 32rpx;
		margin-bottom: 20rpx;
	}
	
	.result {
		margin-top: 20rpx;
	}
	
	.result-title {
		font-weight: bold;
		margin-bottom: 10rpx;
		display: block;
	}
	
	.result-text {
		width: 100%;
		height: 300rpx;
		border: 1rpx solid #ddd;
		border-radius: 10rpx;
		padding: 20rpx;
		font-family: monospace;
		font-size: 24rpx;
		background: #f8f8f8;
	}
</style>